import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { environment } from '../../environments/environment';
import { Observable } from "rxjs/Observable";
import  'rxjs/add/operator/map';
import {Rijksmuseum_models, RootObject, ArtObject} from "../model/rijksmuseum_models";

@Injectable()
export class RijksmuseumService {

  constructor(private http: HttpClient) { } 
  
  searchByArtist(term, apikey): Observable<ArtObject[]>{
    var querie = environment.rmURL + apikey + environment.rmformat + environment.rmsearch + term;
    return this.http.get(querie)
                    .map( res =>{
                      let r = res['artObjects'].map( item => {
                        return new ArtObject(
                          item.links, 
                          item.id, 
                          item.objectNumber, 
                          item.title, 
                          item.hasImage,
                          item.principalOrFirstMaker,
                          item.longTitle, 
                          item.showImage,  
                          item.permitDownload,  
                          item.webImage,  
                          item.headerImage,  
                          item.productionPlaces);
                      });
                      return r;
                    });
  }
}


