export class Rijksmuseum_models {
  
}

export class RootObject {
  elapsedMilliseconds: number;
  count: number;
  countFacets: CountFacets;
  artObjects: ArtObject[];
  facets: Facet2[];
}

export class Facet2 {
  facets: Facet[];
  name: string;
  otherTerms: number;
  prettyName: number;
}

export class Facet {
  key: string;
  value: number;
}

export class ArtObject {
  
  constructor(  
  links: Links,
  id: string,
  public objectNumber: string,
  public title: string,
  hasImage: boolean,
  public principalOrFirstMaker: string,
  public longTitle: string,
  showImage: boolean,
  permitDownload: boolean,
  public webImage: WebImage,
  public headerImage: WebImage,
  public productionPlaces: string[]
){}
}

export class WebImage {
  guid: string;
  offsetPercentageX: number;
  offsetPercentageY: number;
  width: number;
  height: number;
  public url: string;
}

export class Links {
  self: string;
  web: string;
}

export class CountFacets {
  hasimage: number;
  ondisplay: number;
}


