import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { ArtObject } from "../model/rijksmuseum_models";
import { RijksmuseumService } from "../shared/rijksmuseum.service";
import { ArtdetailComponent } from '../artdetail/artdetail.component'

@Component({
  selector: 'app-artlist',
  templateUrl: './artlist.component.html',
  styleUrls: ['./artlist.component.css']
})
export class ArtlistComponent implements OnInit {

  @Input() 
  searchTerm: string = "";
  @Input()
  apikey: string = "";
  listOfArt: ArtObject[];
  selectedArt: ArtObject;
  
  constructor(private rijksmuseumService: RijksmuseumService) { }

  ngOnInit() {
    this.zoekenArtiest();
  }
  
  ngOnChanges( changes: SimpleChanges ){
    if(changes.searchTerm != null)
      this.searchTerm = changes.searchTerm.currentValue;  
    
    if(changes.apikey != null)
      this.apikey = changes.apikey.currentValue;  
   
    this.selectedArt = null;
    this.zoekenArtiest();
  }
  
  zoekenArtiest()
  {
    if(this.searchTerm != null && this.apikey != null && this.searchTerm != '' && this.apikey != '')
      this.rijksmuseumService.searchByArtist(this.searchTerm, this.apikey)
                          .subscribe(
                              data => this.listOfArt = data,
                              error => console.log ('oh oh:' + error)
                            );
  }
  
  selectArt(input: ArtObject){
    this.selectedArt = input;
    console.log('selected: ' + this.selectedArt.title)
  }
}


