// Angular Modules
import { NgModule }      from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from "@angular/common/http";
import { CommonModule } from '@angular/common';  

// Custom Components
import { AppComponent } from './app.component';
import { RijksmuseumService } from "./shared/rijksmuseum.service";
import { ArtlistComponent } from './artlist/artlist.component';
import { ArtdetailComponent } from './artdetail/artdetail.component';

// Module declaration
@NgModule({
	imports     : [BrowserModule, HttpClientModule],
	declarations: [AppComponent, ArtlistComponent, ArtdetailComponent],
	bootstrap   : [AppComponent],
	providers	  : [RijksmuseumService]
})
export class AppModule {
}





